.advantages {
  width: 280px;
}

.advantages__title {
  margin: 0;
  margin-bottom: 40px;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 280px;
  gap: 26px;
}

.advantages__item {
  padding-left: 80px;
}

.advantages__text {
  margin: 0;
}
